import React, { useContext } from 'react'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'

import { Container } from './style'
import { Button } from './../../../components'
import { LangContext } from './../../../layout'
interface INWACProps {
  image: string
  title: string
  description: string
}

export default ({ image, title, description }: INWACProps) => {
  const md = new Remarkable()
  const { lang } = useContext(LangContext)

  return (
    <Container>
      <div className='container'>
        <div className='d-flex flex-wrap'>
          <div className='col-md-6 d-flex flex-column pr-0 pr-md-5'>
            <a
              href='https://nwac.ca'
              target='_blank'
              rel='noreferrer'
              className='my-auto'
            >
              <img src={image} alt='nwac' className='my-auto' />
            </a>
          </div>
          <div className='col-md-6 mt-4 mt-md-0'>
            <h2>{title}</h2>
            <div className='my-5'>
              {ReactHTMLParser(md.render(description))}
            </div>
            <Button
              background='light'
              type='url'
              urlType='external'
              to='https://nwac.ca'
            >
              {lang === 'fr' ? 'Visite nwac.ca' : 'Visit nwac.ca'}
            </Button>
          </div>
        </div>
      </div>
    </Container>
  )
}
