import React, { useContext } from 'react'

import { Container } from './style'
import { Button } from './../../../components'
import { LangContext } from './../../../layout'

interface IIndigenousYouthProps {
  data: {
    image: string
    description: string
    buttonTitle: string
    buttonTitleFr: string
    buttonUrl: string
  }[]
}

export default ({ data }: IIndigenousYouthProps) => {
  const { lang } = useContext(LangContext)

  return (
    <Container>
      <div className='container'>
        <div className='row'>
          <div className='d-flex flex-wrap w-100'>
            {data.map(
              ({
                image,
                description,
                buttonTitle,
                buttonTitleFr,
                buttonUrl,
              }) => {
                return (
                  <div
                    key={description}
                    className='col-md-6 d-flex flex-column my-5'
                  >
                    <img src={image} alt='indigenous' />
                    <p className='my-5'>{description}</p>
                    <Button
                      background='light'
                      className='mt-auto'
                      type='url'
                      to={buttonUrl}
                    >
                      {lang === 'fr' ? buttonTitleFr : buttonTitle}
                    </Button>
                  </div>
                )
              }
            )}
          </div>
        </div>
      </div>
    </Container>
  )
}
