import React, { useContext } from 'react'
import { graphql } from 'gatsby'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import Layout from './../layout/index'
import { SEO, StripeBar, Banner } from './../components'
import { LangContext } from './../layout'
import { NWAC, Features, IndigenousYouth } from './../containers/About'

import Image1 from './../images/about-indigenous-youth-map.svg'
import Image2 from './../images/about-indigenous-youth-resources.svg'
import Image3 from './../images/about-flames.svg'
import Image4 from './../images/NWAC-logo-colour.svg'

interface AboutPageProps {
  location: {
    pathname: string
  }
  pageContext: {
    language: string
    description: string
    title: string
    section11: string
    section12: string
    section2Title: string
    section2Data: string
    section2Description: string
    section3Title: string
    section3Description: string
    seoKeyword: string
    seoDescription: string
  }
}

export default ({
  location,
  pageContext: {
    language,
    description,
    title,
    section11,
    section12,
    section2Title,
    section2Data,
    section2Description,
    section3Title,
    section3Description,
    seoDescription,
    seoKeyword,
  },
}: AboutPageProps) => {
  const md = new Remarkable()
  const { lang } = useContext(LangContext)
  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        description={seoDescription}
        keyword={seoKeyword}
        title={title}
      />
      <Banner>
        <h1>{title}</h1>
        <h2 className='mt-5'>{description}</h2>
      </Banner>
      <StripeBar colors={['#00587F', '#80D0DB', '#5A9DB3', '#95C5BB']} />
      <IndigenousYouth
        data={[
          {
            image: Image1,
            description: section11,
            buttonTitle: 'Get more info',
            buttonTitleFr: 'Pour en savoir plus',
            buttonUrl: '/spotlight',
          },
          {
            image: Image2,
            description: section12,
            buttonTitle: 'See what’s happening near you',
            buttonTitleFr: 'Voir ce qui se passe près de chez toi ',
            buttonUrl: '/pulse',
          },
        ]}
      />
      <Features image={Image3}>
        <h2 className='mb-4'>{section2Title}</h2>
        {ReactHTMLParser(md.render(section2Data))}
        <p className='mt-4'>{section2Description}</p>
      </Features>
      <StripeBar colors={['#80D0DB', '#95C5BB', '#E58630', '#DA5027']} />
      <NWAC
        image={Image4}
        title={section3Title}
        description={section3Description}
      />
    </Layout>
  )
}

export const indexPageQuery = graphql`
  query AboutPageQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`
